<template>
    <div>
        <Toolbar searchBtn backBtn>
            <template v-slot:title>
              Chats
            </template>
            <template v-slot:append>
                <NewChat/>
            </template>
        </Toolbar>
        <v-list 
          v-if="chats && chats.length" 
          class="pt-0 overflow-y-auto scrollable"
        >
          <template v-for="(item, index) in chats">
            <v-divider v-if="index != 0" :key="index"></v-divider>
            <v-list-item
                :key="item.title"
                @click="getChat(item.identifier)"
            >
                <v-list-item-avatar :color="item.title.split(' ').length > 1 ? 'blue' : 'success'">
                  <span class="white--text headline">{{ item.title.charAt(0).toUpperCase() }}</span>
                </v-list-item-avatar>

                <v-list-item-content>
                  <v-list-item-title v-text="item.title"></v-list-item-title>
                </v-list-item-content>
            </v-list-item>
          </template>
          <v-divider></v-divider>
        </v-list>
        <template v-else>
            <p class="text-center pt-4 subtitle-1">You dont`t have chats</p>
        </template>
    </div>
</template>

<script>
import Toolbar from '@/components/Toolbar'
import NewChat from '@/components/NewChat'

export default {
  name: 'Chats',
  components: {
    Toolbar,
    NewChat
  },
  data () {
    return {
      colors: [
        "success",
        "blue"
      ]
    }
  },
  computed: {
    chats () {
      return this.$store.state.chat.chats
    }
  },
  methods: {
    getChat (id) {
      this.$store.dispatch('getChat', id)
    },
    randomInt(min, max) {
      return min + Math.floor((max - min) * Math.random())
    }
  },
  mounted () {
    this.$store.dispatch('getChats')
  }
}
</script>
