<template>
  <div class="text-center">
    <v-dialog
      v-model="dialog"
      width="600"
    >
        <template v-slot:activator="{ on }">
            <v-btn icon v-on="on">
                <v-icon>mdi-plus</v-icon>
            </v-btn>
        </template>

        <v-card>
            <v-form
                v-model="valid"
                ref="form"
                @submit.prevent="addNewConversation"
            >
                <v-card-title
                class="headline grey lighten-2"
                primary-title
                >
                    New chat
                    <v-spacer/>
                    <template v-if="newConversation.users.length > 0">Selected: {{ newConversation.users.length }}</template>
                </v-card-title>

                <v-card-text>
                    <v-text-field 
                        v-model="newConversation.title"
                        placeholder="Chat name"
                    ></v-text-field>
                    <v-list-item-group
                        v-if="friends && friends.length"
                        v-model="newConversation.users"
                        multiple
                    >
                        <v-list-item 
                            v-for="item in friends"
                            :key="item.id"
                            :value="item"
                        >
                            <template v-slot:default="{ active, toggle }">
                                <v-list-item-avatar>
                                    <v-img src="https://cdn.vuetifyjs.com/images/lists/1.jpg"></v-img>
                                </v-list-item-avatar>

                                <v-list-item-content>
                                    <v-list-item-title v-text="item.username"></v-list-item-title>
                                </v-list-item-content>
                                <v-list-item-action>
                                    <v-checkbox
                                        :input-value="active"
                                        :true-value="item"
                                        color="primary"
                                        @click="toggle"
                                    ></v-checkbox>
                                </v-list-item-action>
                            </template>
                        </v-list-item>
                    </v-list-item-group>
                    <template v-else>
                        <p class="text-center pt-4 subtitle-1">
                            You dont`t have friends to add to the chat.<br/>
                            Add friends first on <router-link :to="{name: 'users'}">users</router-link> page.
                        </p>
                    </template>
                </v-card-text>

                <v-divider></v-divider>

                <v-card-actions>
                    <v-spacer></v-spacer>
                    <v-btn text @click="dialog = false">Cancel</v-btn>
                    <v-btn text type="submit" :disabled="newConversation.users.length === 0">Create</v-btn>
                </v-card-actions>
            </v-form>
        </v-card>
    </v-dialog>
  </div>
</template>

<script>
import axios from 'axios'

export default {
    name: "NewChat",
    data () {
        return {
            dialog: false,
            valid: false,
            newConversation: {
                title: '',
                users: []
            }
        }
    },
    computed: {
        friends () {
            return this.$store.state.user.friends
        }
    },
    methods: {
        addNewConversation() {
            if (this.$refs.form.validate()) {
                axios.post( `/chats`, JSON.stringify(this.newConversation)).then((resp) => {
                    console.log(resp.data)
                    this.dialog = false
                    this.$store.dispatch('getChats')
                })
                .catch((err) => {
                    console.log(err)
                })
                this.reset()
            }
        },
    },
    mounted () {
        this.$store.dispatch('getFriends')
    }
}
</script>

<style scoped>
  .v-list-item-group{
    height:inherit;
    max-height: 250px;
    overflow-y: auto;
  }
</style>